


import axios from "axios"


// axios.defaults.baseURL = "http://localhost:1902/" ;   // 


let token = "";
axios.defaults.withCredentials = false;
axios.defaults.headers.common['token'] = token;   //   token  
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';//

import { Toast } from 'antd-mobile';

function loadingToast(msg) {
    Toast.loading(msg, 1, () => {
        console.log('');
    });
}

function showToastNoMask(msg) {
    Toast.info(msg, 2, null, false);
}

function successToast(msg) {
    Toast.success(msg, 1);
}
function failToast(msg) {
    Toast.fail(msg, 1);
}

import { history } from "&";

//  
axios.interceptors.request.use(function (config) {

    let userInfo = window.sessionStorage.userInfo;
    if (userInfo) {
        userInfo = JSON.parse(userInfo);
        token = userInfo.token;
    }
    config.headers.common['token'] = token;   // headers.token 

    // ajax  
    Toast.hide();
    loadingToast('...')
    return config;
}, function (error) {

    failToast("!")
    // ajax   
    return Promise.reject(error);
});

//  
axios.interceptors.response.use(function (response) {
    //  
    console.log(response)

    if (response.data.code == '401') {
        // token   
        history.push("/login");
    }

    setTimeout(() => {
        successToast(response.data.msg);
    }, 500);
    return response;
}, function (error) {
    //  
    failToast("!")
    return Promise.reject(error);
});


// export default axios;

// export const http =axios;

export { axios }


